@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 0.2s ease;
}

.App {
  min-height: 110vh;
  display: flex;
  flex-direction: column;
}

.Toastify__toast-body {
  font-family: "Nunito", sans-serif;
}

body {
  font-family: "Nunito", sans-serif;
  background: rgb(217, 222, 247);
  background: linear-gradient(180deg, rgba(141, 152, 207, 0.28) 0, rgb(240, 243, 255) 100%);
  color: #343434;
}

.disable-scroll {
  overflow: hidden;
}

.skeleton {
  -webkit-box-shadow: 0px 0px 15px -3px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 15px -3px rgba(0, 0, 0, 0.2);
  height: 355px;
  width: 90%;
  margin: 0 auto;
  border-radius: 10px;
  border-radius: 10px;
  padding: 10px;
  animation: infiniteAnimation 2s infinite;
}
.skeleton div {
  width: 100%;
  margin: 0 auto;
  height: 30px;
  background-color: #ebebeb;
  border-radius: 10px;
  margin-bottom: 10px;
}
.skeleton div:nth-child(1) {
  height: 75%;
  margin: 0 auto 10px auto;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.skeleton div:nth-child(3) {
  width: 70%;
  margin: 0 auto;
}

@keyframes infiniteAnimation {
  0%, 100% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
}
.custom-navbar {
  width: 100%;
  background-color: rgb(53, 66, 134) !important;
  background: linear-gradient(120deg, #354286 0%, #2793a7 100%);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 10px 67px;
  color: #fff;
  z-index: 9999;
  backdrop-filter: none !important;
}
.custom-navbar .navbar-brand {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}
.custom-navbar .navbar-brand p {
  color: white;
  margin-bottom: 0;
}
.custom-navbar img {
  width: 40px;
}
.custom-navbar .container-fluid .navbar-toggler {
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.custom-navbar .container-fluid .navbar-toggler:focus {
  outline: none;
  box-shadow: 0 0 0 0;
}
.custom-navbar .container-fluid .navbar-toggler img {
  display: none;
  width: 20px;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .container-fluid .navbar-toggler img {
    display: block;
  }
}
.custom-navbar .navbar-nav {
  margin-left: auto;
  width: 100%;
}
.custom-navbar .navbar-nav li {
  margin-right: 43px;
  border-radius: 5px;
  transition: all 0.2s;
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav li {
    margin-right: 0;
  }
}
.custom-navbar .navbar-nav li img {
  width: 20px;
}
.custom-navbar .navbar-nav li a {
  color: #fff !important;
  font-size: 16px;
  font-weight: 600;
  padding: 3px 10px;
  border-radius: 10px;
  transition: all 0.2s;
}
.custom-navbar .navbar-nav li a:hover {
  background: rgba(255, 255, 255, 0.11);
  transform: scale(1.1);
}
.custom-navbar .navbar-nav li .user-icon-link {
  display: flex;
  align-items: center;
}
.custom-navbar .navbar-nav .navbar-middle-part {
  margin-right: auto;
  margin-left: auto;
  display: flex;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav .navbar-middle-part {
    flex-direction: column;
    align-items: center;
  }
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav .navbar-middle-part .nav-item {
    margin: 0 0 5px 0;
  }
}
.custom-navbar .navbar-nav .input-nav-link {
  margin-right: 0;
}
.custom-navbar .navbar-nav .input-nav-link:hover {
  background: none;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav .input-nav-link {
    justify-content: center;
  }
}
@media only screen and (max-width: 550px) {
  .custom-navbar {
    padding: 10px;
  }
}

.nav-search-wrapper {
