.profile-link {
  position: relative;
  text-align: center;
}
@media only screen and (max-width: 991px) {
  .profile-link {
    width: 10%;
    margin: 4px auto !important;
    display: flex;
    flex-direction: column;
  }
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.profile-link .dropdown {
  animation: fade 0.3s ease-in-out;
  box-shadow: -2px 8px 77px 8px rgba(0, 0, 0, 0.35);
  -webkit-box-shadow: -2px 8px 77px 8px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: -2px 8px 77px 8px rgba(0, 0, 0, 0.35);
  height: 70px;
  width: 120px;
  border-radius: 10px;
  position: absolute;
  transform: translateY(100%) translateX(-30%);
  background: rgba(46, 110, 152, 0.404);
  background-color: rgba(46, 110, 152, 0.404);
  backdrop-filter: blur(20px);
}
.profile-link .dropdown button {
  border: none;
  background-color: transparent;
  color: #ffffff;
  padding: 5px 10px;
  border-radius: 10px;
}
.profile-link .dropdown button:hover {
  transform: scale(1.1);
  background: rgba(255, 255, 255, 0.11);
}
.profile-link .dropdown ul {
  margin: 0;
  padding: 0;
}
.profile-link .dropdown a {
  text-decoration: none;
}
.profile-link .dropdown a:hover {
  background: rgba(255, 255, 255, 0.11);
  transform: scale(1.15);
}
.profile-link .dropdown a span {
  color: #ffffff !important;
}
.profile-link .dropdown li {
  height: 28px;
  margin: 5px auto;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media only screen and (max-width: 991px) {
  .profile-link .dropdown {
    display: flex;
    justify-content: center;
    position: static;
    transform: translate(0);
    margin-bottom: 5px;
  }
}

.sticky-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  z-index: 999;
  background: rgba(46, 110, 152, 0.404);
  background-color: rgba(46, 110, 152, 0.404) !important;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(20px) !important;
  transition: background-color 0.3s;
}

.nav-wrapper {
  height: 70px;
}

.Footer {
  background-color: #efefef;
  margin-top: auto;
  min-height: 40%;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
.Footer .sponsor-scroll::after {
  content: "";
  display: block;
  width: 90%;
  border-bottom: solid 1px rgba(69, 92, 217, 0.5);
  margin: 0 auto;
  padding-bottom: 1%;
}
.Footer .sponsor-scroll {
  min-height: 30%;
  padding: 2% 0;
}
@keyframes slide {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
.Footer .sponsor-scroll .logos {
  overflow: hidden;
  height: 50px;
  white-space: nowrap;
  position: relative;
}
.Footer .sponsor-scroll .logos:before,
.Footer .sponsor-scroll .logos:after {
  position: absolute;
  top: 0;
  width: 20%;
  height: 100%;
  content: "";
  z-index: 2;
}
.Footer .sponsor-scroll .logos:before {
  left: 0;
  background: linear-gradient(to left, rgba(255, 255, 255, 0), #efefef);
}
.Footer .sponsor-scroll .logos:after {
  right: 0;
  background: linear-gradient(to right, rgba(255, 255, 255, 0), #efefef);
}
.Footer .sponsor-scroll .logos:hover .logos-slide {
  animation-play-state: paused;
}
.Footer .sponsor-scroll .logos-slide {
  display: inline-block;
  animation: 25s slide infinite linear;
}
.Footer .sponsor-scroll .logos-slide img {
  height: 50px;
  margin: 0 50px;
}
.Footer .info-panel {
  max-height: 70%;
  padding: 2% 0%;
  overflow: hidden;
  margin: 0 auto;
  display: grid;
  width: 100%;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: auto;
  align-items: center;
  justify-items: center;
  gap: 15px;
}
@media (max-width: 767px) {
  .Footer .info-panel {
    transform: scale(0.9);
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
  }
}
.Footer .info-panel ul {
  font-weight: 600;
  text-align: center;
  font-size: 100%;
  color: #343434;
  padding: 0;
  margin: 0;
  list-style-type: none;
  width: 100%;
}
.Footer .info-panel .social-icons {
  display: grid;
  gap: 10px;
  justify-content: space-around;
}
@media (max-width: 767px) {
  .Footer .info-panel .social-icons {
    width: 80%;
    gap: 15px;
    grid-template-columns: repeat(4, 1fr);
    grid-column: 1/-1;
  }
}

